import React from 'react'
import { Link } from 'react-router-dom'

const DiscoverCreatorCart = (props) => {
	const {user} = props
    return (
        <div className="creator_item creator_card space-y-20 mb-20 back-blur back-glass-effect bg-color-dark bg-cart-dark-color not-blur-bg">
										<div className="avatars flex-column space-y-10">
											<Link to={`/creators/${user._id}`}>
											<div className="cover">
												<img src={user.banner} alt="Avatar" className="img-fluid" />
											</div>
											</Link>
											<div className="media">
												<Link to={`/creators/${user._id}`}>
													<img src={user.avatar} alt="Avatar" className="avatar
														avatar-md" />
												</Link>
											</div>
											<div className="details text-center">
												<div>
													<p className="color_black txt_lg">{user.total_sold_price}
														<span className="txt_sm"> ETH</span></p>
													<p className="color_black txt_sm">Sold</p>
												</div>
												<div>
													<p className="color_black txt_lg">{user.nfts_for_sale}</p>
													<p className="color_black txt_sm">NFT</p>
												</div>
												<div>
													<p className="color_black txt_lg">{user.views}</p>
													<p className="color_black txt_sm">Views</p>
												</div>
											</div>
										</div>
									</div>
    )
}

export default DiscoverCreatorCart
