import React,{useEffect, useState} from 'react'
import HeroPopularCreatorCart from './HeroPopularCreatorCart'
import { useSelector, useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { listUser } from '../actions/userActions'
const HeroPopularCreators = () => {
	const dispatch = useDispatch()
	const userList = useSelector(state => state.userList)
	const {loading , users} = userList
	const [topUsers , setTopUsers] = useState([])

	useEffect(() => {
		dispatch(listUser())

	}, [dispatch])
	useEffect(() => {
	
		setTopUsers(prev => {
			users.sort(function(a, b){return b.total_sold_price - a.total_sold_price})
			return (
				users.slice(0,4)
			)
		})

}, [users.length])
var x = window.matchMedia("(max-width: 990px)")
	useEffect(() => {
		window.addEventListener('resize', () => {

			x = window.matchMedia("(max-width: 990px)")
		}
		);
		
	},[x])
    return (
        <div className="container">
				<div className="row">
					{
						topUsers.map(user => {
							return (
								<div className="col-lg-3">
									<HeroPopularCreatorCart user={user}/>
								</div>
							)
						})
					}
				</div>
				{
					x.matches ? null :
					(
						<div className="mt-60  btn-div" style={{textAlign:"center"}}>	
									<Link className="btn btn-grad btn-dark" to="/register" > Be a creator
									</Link>
								</div>
					)
				}
								
			</div>
    )
}

export default HeroPopularCreators
