import React, { useEffect, useState } from 'react'
import HeroArtCart from './HeroArtCart'
import { useSelector, useDispatch } from 'react-redux'
import { listNfts } from '../actions/nftActions'
import { Link } from 'react-router-dom'
import Loading from './Loading'
const Hero = () => {
	var x = window.matchMedia("(max-width: 990px)")


	const dispatch = useDispatch()
	const nftList = useSelector(state => state.nftList)
	const {loading , nfts} = nftList
	const [nftAuctionList , setNftAuctionList] = useState([])

	useEffect(() => {
		dispatch(listNfts())
		
	}, [dispatch])
	useEffect(() => {
		sortNftList()
	}, [nfts.length])
	const sortNftList = () => {
		setNftAuctionList(prev => {
			const temp = nfts.filter(nft => nft.is_auction == true)
			temp.sort(function(a, b){return b.likes - a.likes})
			return (
				temp.slice(0,3)
			)
		})

		
	}
	useEffect(() => {
		window.addEventListener('resize', () => {

			x = window.matchMedia("(max-width: 990px)")
		}
		);
		
	},[x])
    return (
        <div className="hero__2">
			
			<div className="back-img-div back-img-hero-div" style={{zIndex: "0"}}>
				<img className="back-img back-img-4" src="/img/bg/back-effect.png" /> 
			</div>
			<div className="back-img-div-left back-img-hero-div" style={{zIndex: "0"}}>
				<img className="back-img back-img-4" src="/img/bg/back-effect.png" /> 
			</div>
				<div className="container">
					<div className="space-y-20">
						<h1 className="hero__title text-center" >
							{
								x.matches ? (
								<>
								Discover<br/>digital assets
								</>
								) :
									(
										<>
											Discover digital assets

										</>
									)
								
							}
						</h1>
						{
							x.matches ? null 
							: (
						<p className="hero__text text-center sub-text" >NFT smart contracts for supplying liquidity to multiple websites.</p>

							)
						}
						{/* {
							!x.matches ? null : (
<div className="space-x-20 d-flex justify-content-center hero-btn-div " >
							<Link className="btn btn-grad btn-md"
								to="/explore">View
								market</Link>
						</div>
							)
						} */}
						
					</div>
					<div className="wrapper" >
						{/* <div className="bg-blur-div back-blur back-glass-effect"></div> */}
						<div className="row">
							
							{
								loading ? <Loading />: (
									nftAuctionList.map((n,i) => {
										return (
											<div className="col-lg-4" key={n._id}>
												<HeroArtCart nft={n} />
												{
													x.matches ? (
														i == 0 ? (
															<div className="space-x-20 mb-80 mt-30 d-flex justify-content-center hero-btn-div " >
															<Link className="btn btn-grad btn-md"
																to="/explore">View
																market</Link>
														</div>
														) : null
													) : null
												}
											</div>
										)
									})
								)
							}
							
						</div>
					</div>
					{
						!x.matches ? (
							<div className="mt-50 space-x-20 d-flex justify-content-center hero-btn-div">
							<Link className="btn btn-grad btn-md"
								to="/explore">View
								market</Link>
						</div>
						) : null
					}
				</div>
			</div>
    )
}

export default Hero
