import React,{useState , useEffect} from 'react'
import ContactPopup from '../components/ContactPopup'
import PrivacyCheck from '../components/PrivacyCheck'

const Contact = () => {
	const [contactPopupOpen , setContactPopupOpen] = useState(false)
	useEffect(() => {
		const body =  document.querySelector("body");
		body.classList.add("body-login")
		return(() => {
		body.classList.remove("body-login")

		})
	})
  return (
	  <>
    <div className="contact">
			    <div className="row">
			        <div className="col-md-5 contact__img">
			            <div className="img__wrap">
			                {/* <img src="/img/bg/bg-contact.jpg" alt="" /> */}
			            </div>
			        </div>
			        <div className="col-md-7 contact__content">
			            <div className="container">
			                <div className="content__wrap space-y-20">
			                    <div className="space-y-20">
			                        <h1 className="text-left">Get in touch</h1>
			                        <p className="contact__desc ">Were here to help and answer
			                            any question you might have..</p>
			                    </div>
			                    <div className="box is__big back-blur back-glass-effect">
			                        <div className="space-y-10 mb-0">
			                            <div className="row">
			                                <div className="col-sm-6 space-y-20">
			                                    <div className="space-y-10">
			                                        <span className="nameInput">Email</span>
			                                        <input type="email" className="form-control"
			                                            placeholder="contact@Dopop.com" />
			                                    </div>
			                                    {/* <div className="space-y-10">
			                                        <span className="nameInput">Select Country</span>
			                                        <select className="form-select
			                                            custom-select" aria-label="Default
			                                            select example">
			                                            <option>United States</option>
			                                            <option>Finland</option>
			                                            <option>Norway</option>
			
			                                        </select>
			                                    </div> */}
			                                </div>
			                                <div className="col-sm-6 space-y-20">
			                                    <div className="space-y-10 name-margin-mobile">
			                                        <span className="nameInput">Name</span>
			                                        <input type="text" className="form-control"
			                                            placeholder="john doe" />
			                                    </div>
			                                    
			                                </div>
											<div className="col-12 mt-3">
											<div className="space-y-10">
			                                        <span className="nameInput">Subject</span>
			                                        <select className="form-select
			                                            custom-select" aria-label="Default
			                                            select example">
			
			                                            <option>Service Request</option>
			                                            <option>NFT items</option>
			                                            <option>Wallet</option>
			                                            <option>Purchase</option>
			                                            <option>Support</option>
			                                        </select>
			                                    </div>
											</div>
			                                <div className="col-12 mt-20">
			                                    <div className="space-y-10">
			                                        <span className="nameInput">Message</span>
			                                        <textarea style={{minHeight: "140px"}}
			                                            className="mb-0">
			                                        </textarea>
			                                    </div>
			                                </div>
											<PrivacyCheck />
											<div className="send-btn-div">
											<a href="#" onClick={(e) =>{ e.preventDefault;setContactPopupOpen(true)}} className="btn btn-grad w-full btn-mobile mt-5" placeholder="Create an account">Send</a>
											</div>
										</div>
			                        </div>
			                    </div>
			                </div>
			            </div>
			        </div>
			    </div>
			</div>
			{
				contactPopupOpen ? (
					<ContactPopup contactOpen={contactPopupOpen} exit={() => setContactPopupOpen(false)} />
				) :
				""
			}
			</>
  )
}

export default Contact