import React, { useState , useEffect} from 'react'
import PrivacyCheck from '../components/PrivacyCheck'
import RegisterMessagePopup from '../components/RegisterMessagePopup'

const Register = () => {
	const [selectedFile, setSelectedFile] = useState()
    const [preview, setPreview] = useState()
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
	const [nameError, setNameError] = useState("")
    const [emailError, setEmailError] = useState("")
    const [passwordError, setPasswordError] = useState("")
    const [registerPopupOpen, setRegisterPopupOpen] = useState(false)
	useEffect(() => {
        if (!selectedFile) {
            setPreview(undefined)
            return
        }

        const objectUrl = URL.createObjectURL(selectedFile)
        setPreview(objectUrl)

        return () => URL.revokeObjectURL(objectUrl)
    }, [selectedFile])
	const onSelectFile = e => {
        if (!e.target.files || e.target.files.length === 0) {
            setSelectedFile(undefined)
            return
        }
        setSelectedFile(e.target.files[0])
    }
	const RegisterHandler = (e) => {
		e.preventDefault()
		var nameOk = false
		var emailOk = false
		var passwordOk = false
		if (email) {
			let lastAtPos = email.lastIndexOf("@");
			let lastDotPos = email.lastIndexOf(".");
	  
			if (
			  !(
				lastAtPos < lastDotPos &&
				lastAtPos > 0 &&
				email.indexOf("@@") == -1 &&
				lastDotPos > 2 &&
				email.length - lastDotPos > 2
			  )
			) {

			  setEmailError("Email is not valid");
			}else{
				setEmailError("");
				emailOk = true

			}
		  }else{
			setEmailError("Email is required!")
		  }
		  if(password){
			setPasswordError("")
			passwordOk = true

		}else{
			setPasswordError("Password is required!")
		}
		if(name){
			setNameError("")
			nameOk = true

		}else{
			setNameError("name is required!")
		}
		if(nameOk && passwordOk && emailOk){
			setRegisterPopupOpen(true)
		}
	}
	useEffect(() => {
		const body =  document.querySelector("body");
		body.classList.add("body-login")
		return(() => {
		body.classList.remove("body-login")

		})
	})
    return (
		<>
		
        <div className="edit_profile register">
		<div className="container">

				<div className="container">
					<div className="row">
						<div className="col-lg-3"></div>
						<div className="col-lg-9">
							<div className="right_part space-y-20">
								<h1 className="color_white"> Register new account</h1>
								<p className="color_white" style={{color: "white !important"}}>You can set preferred display name, create your profile URL and
									manage other personal settings.</p>
								<div className="box edit_box w-full space-y-20 back-blur back-glass-effect">
									<div className="row">
										<div className="col-lg-6 account-info">
											<div className="avatars space-x-20 mb-30">
												<div id="profile-container">
													<img id="profileImage" src={preview ? preview : "/img/avatars/avt-2.jpg"} alt="Avatar" className="avatar avatar-lg border-0" />
												</div>
												<div>
													<h6 className="mb-1">Profile photo</h6>
													<p className="mb-1 register-text">We recommend an image of at least 400x400. Gifs work too</p>
													<div id="boxUpload">
														<a href="#" className="btn btn-sm btn-dark"> Upload </a>
														<input id="imageUpload" type="file" name="profile_photo" placeholder="Photo" required="" capture="" onChange={onSelectFile} />
													</div>
												</div>
											</div>
											<h3 className="mb-20">Account info </h3>
											<div className="form-group space-y-10 mb-0">
												<div className="space-y-20">
													<div className="space-y-10">
														<span className="nameInput">Display name</span>
														<input type="text" className="form-control" placeholder="your name" onChange={(e) => setName(e.target.value)} />
														<p className="error">{nameError}</p>
													</div>
													<div className="space-y-10">
														<span className="nameInput">Password</span>
														<input type="text" className="form-control" placeholder="Your Password" onChange={(e) => setPassword(e.target.value)}/>
														<p className="error">{passwordError}</p>

													</div>
													<div className="space-y-10">
														<span className="nameInput d-flex justify-content-between">Email
															{/* <span className="txt_xs color_text">Your email for marketplace notifications</span> */}
														</span>

														<div className="confirm">
															<input type="text" className="form-control" placeholder="Enter email" onChange={(e) => setEmail(e.target.value)}/>
															{/* <a href="#" className="confirm-btn btn btn-dark btn-sm">Confirm</a> */}
														</div>
														<p className="error">{emailError}</p>

													</div>
													<div className="space-y-10">
														<span className="nameInput">Bio</span>
														<textarea style={{minHeight: "110px"}} placeholder="Add your bio" />
													</div> 
												</div>
											</div>
										</div>
										<div className="col-lg-6 social-media">
											<h3 className="mb-20">Social media </h3>
											<div className="form-group space-y-10">
												<div className="space-y-20">
													<div className="d-flex flex-column">
														<span className="nameInput mb-10">Facebook</span>
														<input type="text" className="form-control" placeholder="facebook username" />
														{/* <a className="facebook-btn btn btn-primary mt-10 btn-sm" href="#">
															<i className="ri-facebook-circle-fill"></i>
															Connect to Facebook
														</a> */}
													</div>
													<div className="d-flex flex-column">
														<span className="nameInput mb-10">Twitter</span>
														<input type="text" className="form-control" placeholder="twitter username" />
														{/* <a className="twitter-btn btn btn-primary mt-10 btn-sm" href="#">
															<i className="ri-twitter-fill"></i>
															Connect to Twitter
														</a> */}
													</div>
													<div className="d-flex flex-column">
														<span className="nameInput mb-10">Discord</span>
														<input type="text" className="form-control" placeholder="discord username" />
														{/* <a className="discord-btn btn btn-primary mt-10 btn-sm" href="#">
															<i className="ri-discord-fill"></i>
															Connect to Discord
														</a> */}
													</div>
												</div>
											</div>
											{/* <h3 className="mb-20 mt-40"> Notifications </h3>
											<ul className="space-y-10">
												<li className="d-flex space-x-10 switch_item">
													<input type="checkbox" id="switch1" /><label htmlFor="switch1">Toggle</label>
													<span className="color_text"> Email Notifications </span>
												</li>
												<li className="d-flex space-x-10 switch_item">
													<input type="checkbox" id="switch2" /><label htmlFor="switch2">Toggle</label>
													<span className="color_text"> New Bids </span>
												</li>
												<li className="d-flex space-x-10 switch_item">
													<input type="checkbox" id="switch3" /><label htmlFor="switch3">Toggle</label>
													<span className="color_text"> Item Purchased </span>
												</li>
												<li className="d-flex space-x-10 switch_item">
													<input type="checkbox" id="switch4" /><label htmlFor="switch4">Toggle</label>
													<span className="color_text"> People Followed </span>
												</li>
											</ul> */}
										</div>
									</div>
									{/* <div className="hr"></div> */}
									{/* <p className="color_black">Please take a few minutes to read and understand Stacks Terms of
										Service. To continue, youll need to accept the terms of services
										by checking the boxes.</p> */}
										<PrivacyCheck />
									<a href="#" className="btn btn-grad btn-mobile my-mt-40 btn-full-width" onClick={(e) => RegisterHandler(e)}>Register</a>
								</div>
							</div>
						</div>
					</div>
				</div>
				</div>
			</div>
			{
				registerPopupOpen ? (
					<RegisterMessagePopup RegisterOpen={registerPopupOpen} exit={() => setRegisterPopupOpen(false)} />
				): null
			}
			</>
    )
}

export default Register
