import React from 'react'
import { Link } from 'react-router-dom'

const MarketData = () => {
    return (
        <div className="container" style={{position:"relative"}}>
			{/* <div className="back-img-div-left" style={{top: "-100%"}}>
				<img className="back-img" src="/img/bg/back-effect.png" /> 
			</div> */}
			    <div className="logos__wrap pull-mt" style={{backgroundColor: "transparent" , border: "none" , boxShadow: "none"}}>
			        <div className="row align-items-center justify-content-between">
			            <div className="col-lg-8 market-data-div">
			                <h3 className="section__title market-data-text md:mb-20">Pull market data from our
			                    digital asset API</h3>
			            </div>
			            <div className="connect-wallet-btn-div">
			                <Link className="btn btn-grad" to="/connectwallet">Connect wallet</Link>
			            </div>
			        </div>
			    </div>
			</div>
    )
}

export default MarketData
