
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import LoginMessagePopup from '../components/LoginMessagePopup'

const Login = () => {
	const [loginPopupOpen , setLoginPopupOpen] = useState(false)
	const [email , setEmail] = useState("")
	const [password , setPassword] = useState("")
	const [error , setError] = useState("")
	const [errorPassword , setErrorPassword] = useState("")

	const LoginHandler = (e) => {
		e.preventDefault()
		var passwordIsOk = false
		var emailIsOk = false
		if (email) {
			let lastAtPos = email.lastIndexOf("@");
			let lastDotPos = email.lastIndexOf(".");
	  
			if (
			  !(
				lastAtPos < lastDotPos &&
				lastAtPos > 0 &&
				email.indexOf("@@") == -1 &&
				lastDotPos > 2 &&
				email.length - lastDotPos > 2
			  )
			) {

			  setError("Email is not valid");
			}else{
				setError("");
				emailIsOk= true

			}
		  }else{
			setError("Email is required!")
		  }
		  if(password){
			setErrorPassword("")
			passwordIsOk = true

		}else{
			setErrorPassword("Password is required!")
		}
		if(emailIsOk , passwordIsOk){
			setLoginPopupOpen(true)

		}
	}
	useEffect(() => {
		const body =  document.querySelector("body");
		body.classList.add("body-login")
		return(() => {
		body.classList.remove("body-login")

		})
	})
    return (
		<>
        <div className="edit_profile register login">
		<div className="container">
			
				<div className="container">
					<div className="row">
						<div className="col-lg-7"></div>
						<div className="col-lg-5">
							<div className="right_part space-y-20">
								<h1 className="color_white"> Welcome to Dopop </h1>
								<p className="color_white" style={{color: "white !important"}}>Don't have an account yet? <Link to="/register"> Register </Link>
								</p>
								<div className="box edit_box w-full space-y-20 back-blur back-glass-effect">
									<div className="space-y-10">
										<span className="nameInput">Email </span>
										<div className="confirm">
											<input type="text" className="form-control" placeholder="Enter email"  value={email} onChange={(e) => setEmail(e.target.value)}/>
										</div>
										<p className="error">{error}</p>
									</div>
										<div className="space-y-10">
										<span className="nameInput">Password</span> 
										<div className="confirm">
											<input type="password" className="form-control" placeholder="Enter your password" value={password} onChange={(e) => setPassword(e.target.value)} />
										</div>
										<p className="error">{errorPassword}</p>

									</div>
										{/* <a className="btn btn-white btn-sm color_green"> Click to verify </a> */}
										<a href="#" onClick={(e) => LoginHandler(e)} className="btn btn-grad w-full btn-mobile mt-5" placeholder="Create an account">Login</a>
								</div>
							</div>
						</div>
					</div>
				</div>
				</div>
			</div>
			{
				loginPopupOpen ? (
					<LoginMessagePopup loginOpen={loginPopupOpen} exit={() => setLoginPopupOpen(false)} />
				) :
				""
			}
			</>
    )
}

export default Login
