import React, { useEffect , useRef ,useState} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import CreatorsPageHero from '../components/CreatorsPageHero'
import { useParams } from 'react-router'
import { listNfts } from '../actions/nftActions'
import NftCart from '../components/NftCart'
import { singleUser } from '../actions/userActions'
import Loading from '../components/Loading'
const CreatorsPage = () => {
	const {id}= useParams()
	const dispatch = useDispatch()
	const [sortOpen , setSortOpen] = useState(true)

	const nftList = useSelector(state => state.nftList)
	const {loading , nfts} = nftList
	const signleUser = useSelector(state => state.userSingle)
	const {loading:loadingUser , user} = signleUser
	useEffect(() => {
		dispatch(listNfts())
		// dispatch(singleUser(id))
	}, [dispatch])
	useEffect(() => {
		dispatch(singleUser(id))
	},[id,dispatch])
    return (
        <>
		{
			loadingUser ? <Loading /> :
			(
				<>
				<CreatorsPageHero creator={user}/>
            <div className="container">
				<div className="row justify-content-center">
					<div className="col-lg-3 col-md-7 order-md-0 order-1">
						<div className="profile__sidebar">
							<div className="space-y-40">
								<div className="space-y-10">
									<h5 >About me</h5>
									<div className="box space-y-20 back-blur back-glass-effect bg-color-dark bg-cart-dark-color not-blur-bg">
										<p>
											{user.bio}
										</p>
										<div className="row">
											<div className="col-6">
												<span className="txt_sm color_text">NFTs</span>
												<h4>{user.nfts_for_sale}</h4>
											</div>
											<div className="col-6">
												<span className="txt_sm color_text">Views</span>
												<h4>{user.views}</h4>
											</div>
										</div>
									</div>
								</div>
								<div className="space-y-10">
									<h5>Follow me</h5>
									<div className="box back-blur back-glass-effect bg-color-dark bg-cart-dark-color not-blur-bg">
										<ul className="social_profile space-y-10 overflow-hidden">
											<li>
												<a href="#">
													<i className="ri-facebook-line"></i>
													<span className="color_text">facebook/</span>
													@{user.name}
												</a>
											</li>
											<li>
												<a href="#">
													<i className="ri-messenger-line"></i>
													<span className="color_text"> messenger/</span>
													@{user.name}
												</a>
											</li>
											<li>
												<a href="#">
													<i className="ri-whatsapp-line"></i>
													<span className="color_text"> whatsapp/</span>
													@{user.name}
												</a>
											</li>
											<li>
												<a href="#">
													<i className="ri-youtube-line"></i>
													<span className="color_text">youtube/</span>
													@{user.name}
												</a>
											</li>
										</ul>
									</div>
								</div>
							</div>
							<p className="text-center txt_sm mt-20 color_text">Since 2021</p>
						</div>
					</div>
					<div className="col-lg-9 col-md-12 order-md-1 order-0">
						<div className="profile__content">
							<div className="d-flex justify-content-between">
								<div className="space-x-10" style={{width : "100%"}}>
									{/* <div className="d-flex justify-content-between">
										<ul className="nav nav-tabs d-flex space-x-10 mb-30" role="tablist">
											<li className="nav-item">
												<a className="btn btn-white btn-sm active" data-toggle="tab" href="#tabs-1" role="tab" aria-selected="true">
													NFTs</a>
											</li> */}
											{/* <li className="nav-item">
												<a className="btn btn-white btn-sm" data-toggle="tab" href="#tabs-2" role="tab" aria-selected="false">
													Collections</a>
											</li> */}
										{/* </ul> */}
										
										{/* <div className="dropdown ">
											<button className="btn btn-white btn-sm dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" onClick={() => setSortOpen(prev => !prev)}>
												Recent Active
											</button>
											<div  style={{color:'red'}} className="dropdown-menu" >
												<a className="dropdown-item"  href="#">Action</a>
												<a className="dropdown-item" href="#">Another
													action</a>
												<a className="dropdown-item" href="#">Something
													else here</a>
											</div>
										</div> */}
									{/* </div> */}
			
									<div className="tab-content">
										<div className="tab-pane active" id="tabs-1" role="tabpanel">
											<div className="row mb-30_reset">
												
												{
													loading ? <Loading /> : (
														nfts.map(n => {
															if(n.creator == user._id){
															return (
																<>
																<div className="col-xl-4 col-lg-6 col-md-6">				
																	<NftCart nft={n}/>
																</div>
																
														</>
															)
															}
														})
													)
												}
											</div>
										</div>
										
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
				</>
			)
		}
	
            
        </>
    )
}

export default CreatorsPage
